@media (max-width: 1200px) { 
    .pizzablock__order-details{
        flex-direction: column;
        align-items: center;
        .pizzablock__price{
            margin-bottom: 5px;
        }
    }
    .delivery input{
        width: 130px;
    }
}

@media (max-width: 1050px) { 
    .pizzablock__pizza-items{
        gap: 10px;
    } 
    .pizzablock__order-details{
        flex-direction: column;
        align-items: center;
        .pizzablock__price{
            margin-bottom: 5px;
        }
    }
  
}

@media (max-width: 992px) { 
    .main-block{
        width: 68%;
    }
    .aside-cart{
        width: 32%;
    }
    .pizzablock__pizza-items{
        grid-template-columns: repeat(2, 1fr);
        padding: 2px 10px;
    } 
    .pizzablock__filter-sort{
        font-size: 14px;
        display: grid;
        grid-template-columns: auto auto;
        row-gap: 10px;
    }
    .pizzablock__filter-active{
        font-size: 18px;
    }
    .modal__pizza{
        width: 85%;
    }
    .addIngredients__ingr{
        font-size: 11px;
    }
    .modal__pizza-info {
        display: flex;
        justify-content: center;
    }
    .sort-by-price{
        font-size: 14px;
    }
    .aside-cart__body{
        font-size: 14px;
    }
    .pizzablock__pizza-img{
        width: 100%;
        height: 200px;
    }
    .pizzablock__morefilters{
        width: max-content;
    }
   
}
